  computed: {
    curConfigData () {
      const result = []
      const start = (this.currentPage - 1) * this.pageSize
      let end = this.currentPage * this.pageSize
      end = end > this.configData.length ? this.configData.length : end
      for (let i = start; i < end; ++i) {
        result.push(this.configData[i])
      }
      return result
    }
  },
  methods: {
    resetForm () {
      this.$refs['configForm'].reset()
    },
    query () {
      this.currentPage = 1
    },
    charge (id) {
      this.agencyNo = id
      this.isChargeVisible = true
    },
    configFee () {
      this.configFeeVisible = true
    },
    configFeeDetail () {
      console.log('back')
      if (this.configFeeVal === 'same') {
        this.$router.push({path: '/admin/source/config/sameFee'})
      } else if (this.configFeeVal === 'dis') {
        this.$router.push({path: '/admin/source/config/disFee'})
      }
    },
    back (id) {
      this.backVisible = true
    },
    search: function (id) {
      this.searchDataVisible = true
    },
    dataChange (size, page) {
      this.pageSize = size
      this.currentPage = page
    },
    look (id) {
      console.log('look', id)
    },
    change () {
      this.currentPage = 1
      if (this.config.type === 1) {
        this.columns = this.columns1
        this.configData = this.configData1
      } else if (this.config.type === 2) {
        this.columns = this.columns2
        this.configData = this.configData2
      } else {
        this.columns = []
        this.configData = []
      }
    }
